import React, { useRef } from 'react';
import {
    AriaListBoxOptions,
    AriaOptionProps,
    mergeProps,
    useFocusRing,
    useOption,
} from 'react-aria';
import { ListState } from 'react-stately';
import type { Node } from '@react-types/shared';
import classNames from 'classnames';

import styles from './ListBox.module.scss';

interface OptionProps<V extends object> extends AriaOptionProps {
    item: Node<V>;
    state: ListState<V>;
    variant?: 'dropdown' | 'select';
    gaName?: string;
    selectionMode?: AriaListBoxOptions<V>['selectionMode'];
}

const Option = <V extends object = never>({
    item,
    state,
    variant,
}: OptionProps<V>) => {
    const ref = useRef(null);
    const { optionProps, isSelected, isDisabled, isFocused } = useOption(
        { key: item.key },
        state,
        ref
    );
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.option, {
        [styles[`option--selected`]]: isSelected,
        ['option--selected']: isSelected,
        [styles[`option--disabled`]]: isDisabled,
        [`option--disabled`]: isDisabled,
        [styles[`option--focused`]]: isFocused,
        [`option--focused`]: isFocused,
        [styles[`focus-ring`]]: isFocusVisible && !variant,
        [styles[`focus-ring--select`]]: isFocusVisible && variant === 'select',
        [styles[`focus-ring--dropdown`]]:
            isFocusVisible && variant === 'dropdown',
    });

    return (
        <li
            {...mergeProps(optionProps, focusProps)}
            className={classList}
            ref={ref}
        >
            {item.rendered}
        </li>
    );
};

export default Option;
