import React, { RefObject, useRef } from 'react';
import { AriaListBoxOptions, useListBox } from 'react-aria';
import { ListProps, ListState, useListState } from 'react-stately';
import type { CollectionChildren } from '@react-types/shared';
import classNames from 'classnames';

import Option from './Option';

import styles from './ListBox.module.scss';

export interface ListBoxProps<V extends object> extends AriaListBoxOptions<V> {
    className?: string;
    variant?: 'dropdown' | 'select';
    gaName?: string;
    state?: ListState<V>;
    listBoxRef?: RefObject<HTMLUListElement>;
    maxHeight?: number;
    children?: CollectionChildren<V>;
}

const ListBox = <V extends object>(props: ListBoxProps<V>) => {
    const ref = useRef(null);
    const {
        className,
        variant,
        gaName,
        label,
        state,
        listBoxRef = ref,
        maxHeight,
        selectionMode,
    } = props;
    const generatedState = useListState(props as ListProps<V>);
    const currentState = state ?? generatedState;
    const { listBoxProps, labelProps } = useListBox(
        props,
        currentState,
        listBoxRef
    );

    const classList = classNames(styles.listbox, className, {
        [styles[`listbox--dropdown`]]: variant === 'dropdown',
        [styles[`listbox--select`]]: variant === 'select',
    });

    return (
        <div className={classList}>
            {label && (
                <div className={styles.listbox__label} {...labelProps}>
                    {label}
                </div>
            )}
            <ul
                {...listBoxProps}
                className={styles.listbox__list}
                ref={listBoxRef}
                style={{
                    maxHeight: maxHeight,
                }}
            >
                {[...currentState.collection].map((item) => (
                    <Option
                        key={item.key}
                        item={item}
                        state={currentState}
                        variant={variant}
                        gaName={gaName}
                        selectionMode={selectionMode}
                    />
                ))}
            </ul>
        </div>
    );
};

export default ListBox;
